import router from '@router';
import RouterService from '@services/router-service';
import loginService from '@services/login-service';
import context from '@yl-app-jssdk/context';
/**
 * 
 */
window.pmcEventMsgCatch = {};
let _isFirstLogin = true;

/**
 * 
 */
const init = () => {
  window.pmcEventMsgCatch = {};
  // // 
  _initUserInfo();
  // 
  window.MSdkAssistant.addListener(result => {
    console.log(':', result);
    doEvent(result || {});
  });
  // webview
  router.beforeEach((routeTo, routeFrom, next) => {
    if (
      routeTo &&
      (routeTo.name === 'workbench' || routeTo.name === 'user') &&
      routeFrom &&
      routeFrom.name !== 'login'
    ) {
      closeWebview();
    }
    next();
  });
};

/**
 * 
 * 
 * currentProject: "{"id":"39f90bc7-c9b4-b91a-aae0-cc79f86f0a6b","name":"-","parent_name":"","corp_id":"39f90bbf-971a-31d1-5cb7-155f90c07cb8","parent_id":"39f90bc7-8c64-9a27-1566-a52dfa9d2260","is_end":"1","img_url":"[{\"img_url\":\"https:\/\/minio-scp-sk-st1.uat.cmft.com\/test-zj\/pmc\/publicdata\/projectdata\/1606548207186.jpeg\",\"file_name\":\"timg.jpeg\",\"is_private\":0}]","lat":"0.000000","lng":"0.000000","label":"project","expire_time":"2021-11-25 23:59:59","is_expired":0,"children":[]}"
 * token: "5001f6502569bf6a764471077d6309a66c09e9fde1fb7e8dfd9e4371a693c6fbbbd48b71fb88bd9d3aed56c75625e4454ce6"
 * uid: "92af814e-d03e-4a01-9a56-9a843156ebd0"
 */
const _initUserInfo = () => {
  return new Promise(resolve => {
    if (window.MSdkAssistant.getUserInfo) {
      window.MSdkAssistant.getUserInfo(res => {
        console.log(' =======>>>>>>', res);
        // todo token
        window.pmcEventMsgCatch.login = { event: 'login', data: res };
        // 
        if (res.currentProject) {
          try {
            changeProject(JSON.parse(res.currentProject));
          } catch (err) {
            console.log(err);
          }
        }
        resolve(res);
      });
    } else {
      resolve({});
    }
  });
};

/**
 * 
 */
const checkLogin = () => {
  if (!context.getIsLogged()) {
    router.replace({ name: 'login' });
    openWebview();
    return false;
  }
  return true;
};

/**
 * 
 * @param {*} data
 * {
 *  token: 'token' //  token
 * }
 */
const doSingleLogin = data => {
  // 
  console.log('do Single login', data);
  // loginService.singleSignOn(data).catch(err => {
  //   console.log('===============', err);
  // });
  //  loading 
  // return loginService.loginByCode('15000000000', '6604', 'cjap');
  if (data && data.token) {
    return loginService.singleSignOn({
      tenantCode: 'pmc',
      token: data.token,
    });
  }
};

/**
 * 
 * @param {*} data
 * {
 *  product: '',
 *  code: 'code ',  // 
 *  name: ' ' ,
 *  route: '' ,
 * }
 */
const open = async (data = { product: '' }) => {
  const param = data.additionalInfo || {};
  try {
    // 
    if (param.product && param.product === 'main') {
      router.push(param.router);
      return false;
    }
    // 
    // 
    if (
      _isFirstLogin &&
      window.__module_entry_zjapp__ &&
      window.__module_entry_zjapp__.isFirstTime
    ) {
      // 
      _isFirstLogin = await window.__module_entry_zjapp__.isFirstTime();
    }
    if (param.product === 'zjapp') {
      // 
      window.__module_entry_zjapp__ &&
        window.__module_entry_zjapp__.removeNextPageTransition &&
        window.__module_entry_zjapp__.removeNextPageTransition();

      // 
      if (_isFirstLogin) {
        return false;
      }
    }

    // 
    RouterService.goto(param);
  } catch (err) {
    console.error('open error', err);
  }
};

/**
 * 
 * @param {*} data
 * {
 *  projectId: 'id',
 *  projectName: '',
 * }
 */
const changeProject = data => {
  console.log('change zjapp project', data);
  const id = data.id || '';
  const name = data.name || '';
  if (
    id &&
    window.__module_entry_zjapp__ &&
    typeof window.__module_entry_zjapp__.setCurrentProject === 'function'
  ) {
    window.__module_entry_zjapp__.setCurrentProject(id, name);
  }
};

/**
 * 
 * @param {*} data
 */
export const doEvent = (res = { event: '' }) => {
  console.log('send event message =======>>>', res);
  // 
  if (res.event) {
    window.pmcEventMsgCatch[res.event] = res;
  }
  // 
  switch (res.event) {
    case 'login':
      console.log('do login');
      return doSingleLogin(res.data);
    case 'logout':
      console.log('do logout');
      loginService.logout();
      window.pmcEventMsgCatch = {};
      break;
    case 'open':
      console.log('do open');
      if (checkLogin()) {
        open(res.data);
        openWebview();
      }
      break;
    case 'projectDidChange':
      console.log('change project');
      changeProject(res.data);
      break;
    default:
      break;
  }
};

/**
 * webview
 */
export const openWebview = () => {
  window.MSdkAssistant.show();
};

/**
 * webview
 */
export const closeWebview = (options = {}) => {
  window.MSdkAssistant.hide(options);
};

/**
 * 
 * 1. 
 * 2. 
 */
export const reLogin = () => {
  return _initUserInfo().then(res => {
    return doSingleLogin(res);
  });
};

