import device from '@yl-app-jssdk/device';
import CONST from '@src/config/const';
import localStorageApi from '@yl/localstorage';

let _appVersion;
const getAppVersion = () => {
  if (_appVersion) {
    return Promise.resolve(_appVersion);
  }

  return device.ready().then(() => {
    return new Promise((resolve, reject) => {
      window.MUtils.getAppVersion(
        version => {
          if (!version) {
            console.error('getAppVersion: version');
            reject('app');
          } else {
            _appVersion = version;

            console.warn('appversion:', version);
            resolve(version);
          }
        },
        error => {
          console.error('getAppVersion:', error);
          // window.$log('app', error);
          reject('app');
        }
      );
    });
  });
};
/**
 * app
 * @param cache 
 * @returns {promise}
 */
const getAppFullVersion = (cache = false) => {
  // true
  if (cache) {
    return localStorageApi.getObject(CONST.VERSION);
  }

  let version;
  let webVersion;

  return getAppVersion().then(appVersion => {
    // 1.
    // 1.1.appapp
    //          
    // 1.2.
    //          appweb00
    // 1.3.appapp
    //          appweb00

    // {"app_version":"3.1.5","web_version":"320"}
    try {
      version = localStorageApi.getObject(CONST.VERSION);
    } catch (err) {
      version = {};
    }

    if (version && appVersion === version.appVersion && version.webVersion) {
      webVersion = version.webVersion;
    }

    webVersion = webVersion || '00';

    // 2.
    const oldVersionInfo = localStorageApi.getObject(CONST.VERSION);
    let res = {
      ...oldVersionInfo,
      appVersion: appVersion,
      webVersion: webVersion,
      fullVersion: appVersion + webVersion,
    };
    // appVersion
    localStorageApi.set(CONST.VERSION, res);
    return res;
  });
};
export default {
  getAppVersion,
  getAppFullVersion,
};
