/**  app store  */

import Context from '@yl-app-jssdk/context';
import router from '@router';
import store from '@store';
import singleSpaConfig from '@src/single-spa-config';

export default {
  init(user) {
    Context.updateUser({
      id: '9e3dd387adbc63d82050cfff9961b2b9',
      unionId: 'ec62a22211dc25086f148b5aac64638c9c6a',
      nickname: 'Demo',
      realname: 'Demo',
      username: 'Demo',
      userId: '9e3dd387adbc63d82050cfff9961b2b9',
      userCode: user.phone,
      userName: 'Demo',
      userMobile: user.phone,
      avatarUrl: '',
      avatarPath: '',
      mobile: user.phone,
    });
    store.commit('app/INIT_STATUS', true);

    // 
    if (window.location.hash.startsWith('#/login')) {
      router.replace('/workbench');
    }

    window.GLOBAL_ENV.PRODUCTS = ['gcxt'];
    // gcxtapp store
    return singleSpaConfig.loader('gcxt');
  },
};
