import network from '@yl-app-jssdk/network';
import { doUpdate } from '@yl-app-jssdk/hot-update';
import version from '@src/common/version';
import device from '@yl-app-jssdk/device';
import http from '@yl-app-jssdk/http';
import localstorage from '@yl/localstorage';
import CONST from '@src/config/const';
// import db from './db';
import { showVersionAlert, progress } from '@src/common/com-cmps-handler';

const WEB_VERSION_UPDATE_INFO = 'web_version_update_info';

/**
 * 
 *
 */
let _saveAppVersion = webVersion => {
  if (!webVersion) {
    throw new Error('APPwebVersion');
  }

  return version.getAppVersion().then(function(appVersion) {
    localstorage.set(
      CONST.VERSION,
      {
        appVersion: appVersion,
        webVersion: webVersion,
      },
      localstorage.level.global
    );
  });
};

let _reOpenIndexPage = () => {
  console.log('');
  window.location.href =
    window.location.href.replace(/#.*$/, '') + '?v=' + new Date().getTime();
};

/**
 * 
 *
 * @param {*} updatedWebInfo 
 * @returns
 */
let _execWebContentUpdate = updatedWebInfo => {
  if (!updatedWebInfo || !updatedWebInfo.versionCode) {
    console.log('web');
    return Promise.resolve();
  }
  if (!updatedWebInfo.downloadUrl) {
    return Promise.reject('downloadUrl');
  }

  progress.start('');
  return version.getAppFullVersion().then(version => {
    // webweb
    if (updatedWebInfo.versionCode > version.webVersion) {
      console.log('');
      return doUpdate(updatedWebInfo.downloadUrl, percent => {
        progress.update(percent);
      })
        .then(() => {
          // web
          _saveAppVersion(updatedWebInfo.versionCode);

          if (updatedWebInfo.popup == 1) {
            localstorage.saveString(
              WEB_VERSION_UPDATE_INFO,
              updatedWebInfo.update_info,
              localstorage.level.global
            );
          }
          console.log('');

          // return db.closeAllDB().then(() => {
          // web
          _reOpenIndexPage(true);
          // 
          return Promise.resolve();
          // });
        })
        .catch(error => {
          return Promise.reject(error);
        })
        .finally(() => {
          progress.finish();
        });
    } else {
      return Promise.resolve();
    }
  });
};

export default {
  /**
   * 
   *
   * @param {string} appCode
   * @returns
   */
  checkAppUpdate: appCode => {
    return network
      .getHasNetwork()
      .then(hasNetwork => {
        // 1. 
        if (!hasNetwork) {
          // 1.1 
          return Promise.reject('');
        }
        // 2. 
        return Promise.resolve(device.getDeviceInfo());
      })
      .then(device => {
        // 2.1 
        let platform = device && device.platform;
        if (!platform) {
          return Promise.resolve('');
        }
        // 3. 
        return version.getAppFullVersion().then(localVersion => {
          return http
            .ajax({
              url: `${window.GLOBAL_ENV.APP_TOOL_API}/update/app-version/get-version`,
              params: {
                platform: platform,
                appCode: appCode,
                appVersion: localVersion.fullVersion,
              },
              method: 'get',
            })
            .then(({ data: res }) => {
              if (res.errCode !== 0 || !res.data) {
                return Promise.resolve('');
              }

              return { updatedVersion: res.data, localVersion: localVersion };
            });
        });
      })
      .then(function(versions) {
        let updatedVersion = versions.updatedVersion;
        let localVersion = versions.localVersion;
        let currentVersion = localVersion.appVersion;

        if (
          updatedVersion.hotUpdateVersion &&
          updatedVersion.hotUpdateVersion.versionCode > localVersion.webVersion
        ) {
          // 
          return Promise.resolve({
            type: 'WebVersion',
            reason: '',
            updated: updatedVersion,
          });
        }
        // 4. 
        // 4.1. 
        if (
          !updatedVersion.versionCode ||
          currentVersion == updatedVersion.versionCode
        ) {
          return Promise.reject('');
        }
        // 4.2. ,
        if (currentVersion > updatedVersion.versionCode) {
          return Promise.reject('');
        }
        return Promise.resolve({
          type: 'AppVersion',
          updatedVersion: updatedVersion,
        });
      });
  },

  /**
   * 
   *
   * @param {*} updatedVersion
   * @param {string} [cancelText='']
   * @param {*} [cancelFunc=null]
   */
  showUpgradePopup: (
    updatedVersion,
    cancelText = '',
    cancelFunc = null
  ) => {
    updatedVersion.updateInfo = updatedVersion.updateInfo || '';

    let details =
      updatedVersion.updateInfoStr && updatedVersion.updateInfoStr.split('|');

    let showCancel = false;

    if (updatedVersion.updateType !== 'compulsive') {
      // 
      showCancel = true;
    }
    details = details || [];
    return showVersionAlert(
      updatedVersion.versionName,
      details,
      updatedVersion.downloadUrl,
      showCancel,
      cancelText,
      cancelFunc
    );
  },
  /**
   * 
   *
   * @param {*} webVersion
   * @returns
   */
  doUpdate: webVersion => {
    return _execWebContentUpdate(webVersion);
  },
};
